import { baseURL } from './urlConfig';
import { toast } from 'react-toastify';
export const DATE_FORMAT = 'dd/MM/yyyy';
export const imgURL = url => url ? `${baseURL}/file/${url}` : null;
export const getQueryParams = search => {
  if (!search || typeof search !== 'string') return search;
  const s = search.substring(1);
  const qs = s.split('&');
  const queries = {};
  qs.forEach(q => {
    const t = q.split('=');
    queries[t[0]] = t[1];
  });
  return queries;
};
export const getLocation = (success, error) => {
  if (!navigator.geolocation) {
    alert('Geolocation is not supported.');
    return;
  }

  navigator.geolocation.getCurrentPosition(success, error || (err => alert(err.message)));
};
