export const isTrue = value => {
  if (!value) return false;

  if (typeof value === 'string') {
    return value.toLowerCase() !== 'false';
  }

  return true;
};
export const CUSTOM_LOGIC_FUN = 'function customLogic(res, q) {';
export const CUSTOM_VALIDATION_FUN = 'function validate(e, q) {';
/*
abstract the filter method into a function that we can reuse to filter by any boolean property.
Then call it later like so:
filter(users, 'isAdmin')
*/

export const filter = (array, property) => array.filter(item => item[property]);
export const getCustomLogicFun = logic => {
  let body = 'try{' + logic + '}catch(e){console.error(e)}'; // eslint-disable-next-line no-new-func

  return Function('res', 'q', body);
};
export const getCustomValidationFun = validation => {
  let body = 'try{' + validation + '}catch(e){console.error(e)}'; // eslint-disable-next-line no-new-func

  return Function('res', 'q', body);
};
export const isNull = value => value === undefined || value === null;
export const stringifyMap = map => {
  let str = [];

  for (let [k, v] of map) {
    str.push(JSON.stringify(k) + ':' + JSON.stringify(v));
  }

  return '{' + str.join(',') + '}';
};
export const checkIfBrowserHasInternet = () => {
  return window.navigator.onLine ? true : false;
};
