export const findUniqValues = arr => {
  const x = {};
  return arr.filter(e => {
    return x.hasOwnProperty(e) ? false : x[e] = true;
  });
};
export const removeTimeFormDate = date => {
  const _date = convertToDateObj(date);

  if (_date) return new Date(_date.toDateString());
  return date;
};

const convertToDateObj = d => {
  if (typeof d === 'string' && (d[0] === "'" && d[d.length - 1] === "'" || d[0] === '"' && d[d.length - 1] === '"')) {
    d = d.substring(1, d.length - 1);
  }

  const _date = new Date(d);

  if (_date.toString().includes('Invalid Date')) return null;
  return _date;
};

export const convertUTCDateToLocalDate = date => {
  var newDate = new Date(date.getTime() - date.getTimezoneOffset() * 60 * 1000);
  return newDate;
};