import { openDB } from 'idb';
const dbPromise = openDB('rsphims-data-entry-app', 1, {
  upgrade(db) {
    db.createObjectStore('dataset-sections');
    db.createObjectStore('dataset-sections-overview');
    db.createObjectStore('dataset-data-elements');
    db.createObjectStore('dataset-data-value');
    db.createObjectStore('dataset-report-confirmation');
    db.createObjectStore('facility-cover-image');
    db.createObjectStore('failed-reports');
    db.createObjectStore('report-backup');
  }

});
export async function setStore(storeName, key, val) {
  return (await dbPromise).put(storeName, val, key);
} // DATASET SECTIONS

export async function getDatasetSection(id) {
  return (await dbPromise).get('dataset-sections', id);
}
export async function delDatasetSection() {
  return (await dbPromise).delete('dataset-sections', id);
} // DATASET OVERVIEW

export async function getSectionsOverview(id) {
  return (await dbPromise).get('dataset-sections-overview', id);
}
export async function setSectionsOverview(id, val) {
  return (await dbPromise).put('dataset-sections-overview', val, id);
}
export async function clearSectionsOverview() {
  return (await dbPromise).clear('dataset-sections-overview');
}
export async function delSectionsOverview(id) {
  return (await dbPromise).delete('dataset-sections-overview', id);
} // DATASET DATA ELEMENTS

export async function getDataElementsForDataset(dataSet) {
  return (await dbPromise).get('dataset-data-elements', dataSet);
}
export async function delDataElementsForDataset(id) {
  return (await dbPromise).delete('dataset-data-elements', id);
}
export async function clearDataElementsForDataset() {
  return (await dbPromise).clear('dataset-data-elements');
} // REPORT DATA VALUES

export async function setDataValues(dataSet, values) {
  return (await dbPromise).put('dataset-data-value', values, dataSet);
}
export async function getDataValues(id) {
  return (await dbPromise).get('dataset-data-value', id);
}
export async function clearDataValues() {
  return (await dbPromise).clear('dataset-data-value');
} // REPORT CONFIRMATION

export async function setConfirmedReport(key, val) {
  return (await dbPromise).put('dataset-report-confirmation', val, key);
}
export async function delConfirmedReport(key) {
  return (await dbPromise).delete('dataset-report-confirmation', key);
}
export async function getConfirmedReport(key) {
  return (await dbPromise).get('dataset-report-confirmation', key);
}
export async function keysConfirmAPI() {
  return (await dbPromise).getAllKeys('dataset-report-confirmation');
} // REPORT BACKUP

export async function clearReportBackup() {
  return (await dbPromise).clear('report-backup');
}
export async function getReportBackup(key) {
  return (await dbPromise).get('report-backup', key);
} // Basic images

export async function getImage(id) {
  return (await dbPromise).get('facility-cover-image', id);
}
export async function clearImage() {
  return (await dbPromise).clear('facility-cover-image');
} // Basic idb functions - global assets

export async function delFailedRecord(key) {
  return (await dbPromise).delete('failed-reports', key);
}
export async function keysFailedAPI() {
  return (await dbPromise).getAllKeys('failed-reports');
}
export async function getFailedAPIDataByKey(key) {
  return (await dbPromise).get('failed-reports', key);
}